html,
body,
#root {
  height: 100%;
  width: 100%;
}

body {
  font-family: system-ui;
  margin: 0;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

.bg-video {
  background-size: cover;
  background: no-repeat center;
  user-select: none;
  width: 90%;
  margin-bottom: 10px;
  z-index: 1;
  /*custom styles, for example grayscale appearance*/
  filter: saturate(120%);
}